nk{50%{border-color:transparent}}@keyframes blink{50%{border-color:transparent}}@-webkit-keyframes mouseScroll{0%{-webkit-transform:translateY(0);transform:translateY(0)}100%{-webkit-transform:translateY(6px);transform:translateY(6px)}}@keyframes mouseScroll{0%{-webkit-transform:translateY(0);transform:translateY(0)}100%{-webkit-transform:translateY(6px);transform:translateY(6px)}}@-webkit-keyframes marquee{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(-100%, 0);transform:translate(-100%, 0)}}@keyframes marquee{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(-100%, 0);transform:translate(-100%, 0)}}

